.swiftype-widget .autocomplete,
.swiftype-widget .autocomplete ul {
    background-color: #fff;
    display: block;
    list-style-type: none;
    margin: 0;
    padding: 0;
    text-align: left
}

.swiftype-widget .autocomplete {
    border-left: 1px solid rgba(0, 0, 0, .1);
    border-right: 1px solid rgba(0, 0, 0, .1);
    position: absolute
}

.swiftype-widget .autocomplete ul {
    border-bottom: 1px solid rgba(0, 0, 0, .1);
    max-height: 400px;
    overflow: auto;
    width: 100%
}

.swiftype-widget .autocomplete ul li {
    border-bottom: 1px solid rgba(0, 0, 0, .1);
    cursor: pointer;
    padding: 12px 12px 12px 24px;
    list-style-type: none;
    margin: 0
}

.swiftype-widget .autocomplete ul li.active {
    background-color: #fff;
    border-bottom: 1px solid rgba(0, 0, 0, .1);
    -webkit-box-shadow: none;
            box-shadow: none;
    background: rgba(0, 0, 0, .05)
}

.swiftype-widget .autocomplete ul li p.title {
    font-size: 15px;
    line-height: 24px;
    font-weight: 400;
    color: #4264fb
}

.swiftype-widget .autocomplete li .sections {
    margin-top: 6px;
    color: #999
}

.swiftype-widget .autocomplete ul li em {
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
    text-decoration: none;
    color: inherit
}

.swiftype-widget .autocomplete ul li.active p.title {
    color: #314ccd;
    text-shadow: none
}

.swiftype-widget .autocomplete ul li.active .sections {
    text-shadow: none;
    color: inherit
}

.swiftype-widget .autocomplete li p {
    padding: 0
}

.swiftype-widget .autocomplete li .sections .section {
    display: inline
